import chosetime from '../../components/focutime.vue';
import chosedate from '../../components/focudate.vue';
import noconetnt from '@/components/noconetnt.vue'
export default {
	data() {
		return {
			firsttime:'',
			lasttime:'',
			date:"",
			type:'',
			idxss:'',
			state:{
				focutime:false,
				focudate:false
			},
			list:[]
		};
	},
	onLoad: function (options) {
		var timestamp = Date.parse(new Date());
		this.firsttime = this.getLocalTime(timestamp-(7*24*60*60*1000));
		this.lasttime = this.getLocalTime(timestamp);
		setTimeout(function () {
			
		}, 1000);
		uni.startPullDownRefresh();
	},
	onPullDownRefresh() {
		setTimeout(function () {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	onShow(){
		this.getinfo();
	},
	methods:{
		godel(id){
			uni.navigateTo({
				url:'../homedetail/homedetail?id='+id
			})
		},
		getinfo(){
			uni.request({
				url: this.$api+'/weixin/shishenxin/homeworkmxlist',
				data: this.getmd5({
					startTime:this.firsttime,
					endTime:this.lasttime,
					openId:uni.getStorageSync('openId')
					// openId:'onR7C05SKrVWwNSZrlQgPbMEL4t4'
				}),
				header: {
					'content-type': 'application/json'
				},
				method: "POST",
				success: (res) => {
					if (res.data.code != 200) {
						uni.showToast({
						icon: 'none',
						title: res.data.msg,
						duration: 2000
						});
					} else {
						this.list = [];
						uni.hideLoading();
						var data = res.data.data;
						this.list = data;
					}
				},
				fail: () => {
					uni.hideLoading();
					uni.showToast({
					icon: 'none',
					title: '获取失败',
					duration: 2000
					});
				}
			});
		},
		idxs(e){
			this.idxss = e;
		},
		ft(e){
			this.firsttime = e;
		},
		lt(e){
			this.lasttime = e;
			this.state.focudate = false;
		},
		chosetimedFun(e){
			if(this.type == 'firsttime'){
				if(e>this.lasttime){
					uni.showToast({
						icon:'none',
						title: '日期选择错误',
						duration: 2000
					});
				}else{
					this.firsttime = e;
				}
			}else{
				if(e<this.firsttime){
					uni.showToast({
						icon:'none',
						title: '日期选择错误',
						duration: 2000
					});
				}else{
					this.lasttime = e;
				}
			}
		},
		focutime(type){
			if(type == 'firsttime'){
				this.date = this.firsttime;
				this.type = 'firsttime';
			}else{
				this.date = this.lasttime;
				this.type = 'lasttime';
			}
			this.state.focutime = true;
		},
		closeChosetimeModel: function() {
			this.state.focutime = false;
		},
		getLocalTime(nS) {     
		    return new Date(nS).toJSON().substr(0, 19).replace('T', ' ').replace(/-/g, '-').slice(0,10);
		}  
	},
	components: {
		chosetime,
		chosedate,
		noconetnt
	}
}
